<template>
	<div id="after-sale">
		<common-header :title='title'></common-header>
		<div class="list-wrap">
			<div class="list-item">
				<div class="good-img">
					<img class="goods-img" src="../../assets/pic.png" />
				</div>
				<!-- 商品信息 start -->
				<div class="content">
					<div class="goods-name">酷美人 夜光石手链 男女款萤石手链手串手环饰品</div>
					<div class="goods-norms">颜色：红色 尺寸：大号</div>
					<div class="goods-prices">
						<span class="price"><em class="price-icon">￥</em>180</span>
						<span class="num fl-right"><em>x</em>1</span>
					</div>
				</div>
			</div>
		</div>
		<div class="reason" @click="showOT">
			<span>退货原因</span><span :class="{selected:isSelected}">{{returnReason}}</span>
			<img class="rt-arrow" src="../../assets/arrow_gray.png"/>
		</div>
		
		<div class="refund">
			<div class="title">
				退款说明<span>(可不填)</span>
			</div>
			<div class="refund-text">
				<textarea name="refund" placeholder="请输入退款说明" maxlength="200" @input="getTextarea" ref="textArea"></textarea>
				<span>{{textNum}}/200</span>
			</div>
		</div>
		
		<div class="uploadImg">
			<div class="title">上传凭证,最多3张</div>
			<div class="publish-imgs clearfix">
				<ul>
					<li>
						<img class="publish-icon" src="../../assets/pic.png"/>
						<div class="del-btn">
							<img src="../../assets/del-icon.png"/>
						</div>
					</li>
					<li>
						<img class="publish-icon" src="../../assets/pic.png"/>
						<div class="del-btn">
							<img src="../../assets/del-icon.png"/>
						</div>
					</li>
					<li>
						<img class="publish-icon" src="../../assets/common_btn_add_pic2@2x.png"/>
						<input 
							type="file" 
							accept="image/*" 
							name="file[]" 
							id="publish-img" 
							@change="uploadImg($event,index)"/>
					</li>
				</ul>
			</div>
		</div>
		
		<div class="submit-btn" @click="submitApplication">提交申请</div>
    <div class="shadow" v-if="showOrderType">
      <BottomPopup :itemList="itemList" @selItem="getBottomPopup"></BottomPopup>
    </div>
    
    
	</div>
</template>

<script>
import BottomPopup from '@/components/Popup/BottomPopup'
import CommonHeader from '@/components/Header/CommonHeader'
	export default {
		data() {
			return {
				textNum:0,
				itemList:[
					{text:"商品与描述不符",id:1},
					{text:"少件/漏发",id:2},
					{text:"卖家发错货",id:3},
					{text:"未按约定时间发货",id:4},
					{text:"其他原因",id:5}
				],
        showOrderType: false,
        title:'申请售后',
        returnReason:'请选择退货原因',
        isSelected:false,
			}
		},
		components: {
				BottomPopup,
				CommonHeader
		},
		methods:{
			getTextarea(){
				let textNum = this.$refs.textArea.value.length
				this.textNum = textNum
			},
			showOT(){
        this.showOrderType = true
      },
			getBottomPopup(res){
				console.log(res);
				if(res != 'cancel'){
					this.returnReason = res.text
					this.isSelected = true
				}
				this.showOrderType = false
			},
			submitApplication(){
				this.$popup({
	        content:'您的申请已提交,请等待商家处理?',
	        showCancel:false
	      }).then((res) => {
	        console.log(res)
	      })
			},
		}

	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#after-sale {
		height: 100%;
		background: #f0f0f0;
		.list-wrap {
			background: #fff;
		}
		.shop-info {
			position: relative;
			width: 100%;
			height: 1.17rem;
			line-height: 1.17rem;
			padding: 0 0.4rem;
			font-size: 0.37rem;
			color: #333;
			&:after {
				@include bottom;
			};
			.shop-label {
				color: $label-color;
			}
		}
		.active {
			color: #fe4436;
		}
		.negative {
			color: #999;
		}
		.normal {}
		.list-item {
			position: relative;
			display: flex;
			padding: 0.21rem 0.32rem;
			/*border-bottom: 1px solid #cccccc;*/
			&:after {
				@include bottom;
			};
			.good-img {
				// width: 2.13rem;
				// height: 2.13rem;
				border: 1px solid #dedede;
				border-radius: 0.11rem;
				.goods-img {
					width: 2.13rem;
					height: 2.13rem;
				}
			}
			.content {
				flex: 1;
				margin-left: 0.35rem;
				display: flex;
				justify-content: space-around;
				flex-direction: column;
				.goods-name {
					font-size: 0.37rem;
					color: $text-color;
					@include textflow-two;
				}
				.goods-norms {
					color: $text-color2;
					font-size: 0.32rem;
					@include textflow-one;
				}
				.goods-prices {
					.price {
						color: $price-color;
						font-size: 0.43rem;
						.price-icon {
							font-size: 0.32rem;
						}
					}
					.num {
						color: $text-color2;
						font-size: 0.32rem;
						>em {
							font-size: 0.24rem;
						}
					}
				}
			}
		}
		.reason{
			position: relative;
			height: 1.17rem;
			line-height: 1.17rem;
			width: 100%;
			padding: 0 0.4rem;
			border-bottom:1px solid $border-color;
			background: #fff;
			>span{
				&:nth-of-type(1){
					font-size: 0.4rem;
					color: #333;
				};
				&:nth-of-type(2){
					float: right;
					font-size: 0.37rem;
					padding-right: 0.45rem;
					color: #999;
				};
			}
			.selected{
				color: #666 !important;
			}
			.rt-arrow{
				@include rt-arrow-sm;
			}
		}
		.refund{
			position: relative;
			min-height: 3.33rem;
			background: #fff;
			padding: 0.4rem 0.4rem 0.24rem;
			.title{
				font-size: 0.4rem;
				color: #333;
				>span{
					padding-left: 0.13rem;
					font-size: 0.35rem;
					color: #666;
				}
			}
			textarea{
				@include textarea;
				font-size: 0.37rem;
				padding:0.27rem 0;
				height: 2.08rem;
			}
			.refund-text{
				>span{
					position: absolute;
					right: 0.4rem;
					bottom: 0.24rem;
					font-size: 0.35rem;
					color: #999;
				}
			}
		}
		.uploadImg{
			margin-top: 0.27rem;
			width: 100%;
			background: #fff;
			.title{
				font-size: 0.4rem;
				color: #333;
				padding: 0.37rem 0.4rem 0;
			}
			.publish-imgs{
				padding: 0.51rem 0 0.55rem 0.4rem;
			}
			ul{
				>li{
					position: relative;
					float: left;	
					margin-right: 0.4rem;
				}
				.del-btn{
					position: absolute;
					top: 0;
					right: 0;
					width: 0.43rem;
					height: 0.43rem;
					background: rgba(0, 0, 0, 0.3);
				}
				input{
					position: absolute;
					top: 0;
					left: 0;
					opacity: 0;
					width: 2.8rem;
					height: 2.8rem;
				}
				.publish-icon{
					width: 2.8rem;
					height: 2.8rem;
				}
			}
		}
		.submit-btn{
			position: fixed;
			bottom: 0;
			width: 100%;
			font-size: 0.45rem;
			height: 1.31rem;
			line-height: 1.31rem;
			text-align: center;
			color: #fff;
			background: #ccc;

		}
		.active{
			@include bg-color;
		}
	}
</style>


